          }"
        />
      </router-link>
      <div class="card-title">
        <div class="row" style="font-size: 20px; font-weight: 600">
          <div class="col-12 mt-3">
            <router-link
              :to="'/service-company?service_company_id=' + id"
              class="myrouter"
            >
              {{ this.name }}
            </router-link>
          </div>
        </div>
      </div>
      <div
        class="card-text text-start ms-3"
        style="color: #585858; font-size: 11px"
      >
        <div
          class="row mt-3"
          :style="{ direction: $store.state.lang == 'en' ? 'ltr' : 'rtl' }"
        >
          <div class="col-5">
            <p>
              <Icon
                icon="material-symbols:location-on-rounded"
                height="15"
                width="15"
              />
              {{
                $store.state.lang == "en"
                  ? location.place
                  : $store.state.lang == "ar"
                  ? ""
                  : ""
              }}{{ ", " + city }}
            </p>
          </div>
          <div class="col-7">
            <p>
              <Icon icon="fa6-solid:truck-moving" height="15" width="15" />
              {{ service_show }}
            </p>
          </div>
        </div>
      </div>
      <div class="card-text d-flex justify-content-center align-items-center">
        <div class="card-link">
          <div
            class="row mb-2"
            :style="{ direction: $store.state.lang == 'en' ? 'ltr' : 'rtl' }"
          >
            <div class="col-12 call">
              <button
                class="call_email"
                @click.stop="show_hide('phone', this.new_id)"
              >
                <Icon icon="ic:round-call" color="#e57c23" />
                {{
                  $store.state.lang == "en"
                    ? "Call"
                    : $store.state.lang == "ar"
                    ? ""
                    : ""
                }}
              </button>
              <div
                class="container mt-5"
                style="display: none"
                :id="'phone' + this.new_id"
                @focusout.stop="show_hide('phone', this.new_id)"
              >
                <div class="block">
                  {{
                    $store.state.lang == "en"
                      ? "Phone"
                      : $store.state.lang == "ar"
                      ? ""
                      : ""
                  }}: {{ this.phone }}
                  <button
                    class="call_email2 w-100"
                    :id="'ph_btn' + this.new_id"
                    @click.stop="copy('phone')"
                  >
                    {{
                      $store.state.lang == "en"
