                      ? ""
                      : ""
                  }}: {{ this.email }}
                  <button
                    class="call_email2 w-100"
                    :id="'em_btn' + this.new_id"
                    @click.stop="copy('email')"
                    @blur.stop="show_hide('email', this.new_id)"
                  >
                    {{
                      $store.state.lang == "en"
                        ? "Copy"
                        : $store.state.lang == "ar"
                        ? ""
                        : ""
                    }}
                  </button>
                </div>
              </div>
              <button
