                @click.stop="show_hide('whatsapp', this.new_id)"
              >
                <Icon icon="logos:whatsapp-icon" color="#e57c23" />{{
                  $store.state.lang == "en"
                    ? "Whatsapp"
                    : $store.state.lang == "ar"
                    ? ""
                    : ""
                }}
              </button>
              <div
                class="container mt-5"
                style="display: none"
                :id="'whatsapp' + this.new_id"
                @focusout.stop="show_hide('whatsapp', this.new_id)"
              >
                <div class="block">
                  {{
                    $store.state.lang == "en"
                      ? "Whatsapp"
                      : $store.state.lang == "ar"
                      ? ""
                      : ""
                  }}: {{ this.whatsapp }}
                  <div class="d-flex justify-content-center align-items-center">
                    <a
                      class="call_email2 whats_app w-100"
                      :href="'https://wa.me/' + this.whatsapp"
                      target="_blank"
                      :id="'wa_btn' + this.new_id"
                      >{{
                        $store.state.lang == "en"
                          ? "Go to whatsapp"
                          : $store.state.lang == "ar"
                          ? " "
                          : ""
                      }}
                    </a>
