<template>
  <div class="admin-users">
    <SideBar>
      <div
        class="row w-100 bg-light p-5 flex-center"
        ref="target2"
        style="border-radius: 0px 0px 10px 10px; border: none"
      >
        <div
          class="col-12 add-real mb-5 w-100"
          style="height: fit-content; width: 100%"
        >
          <div
            class="w-100 h-100"
            :class="$store.state.lang == 'en' ? 'text-start' : 'text-end'"
          >
            {{
              $store.state.lang == "en"
                ? "Users"
                : $store.state.lang == "ar"
                ? ""
                : ""
            }}
            ({{ Object.keys(users).length }})
          </div>
        </div>
        <div
          class="col-12 add-real mb-5 w-100"
          style="height: fit-content; width: 100%"
        >
          <div
            class="w-100 h-100"
            :class="$store.state.lang == 'en' ? 'text-start' : 'text-end'"
          >
            <input
              type="text"
              class="myinp"
              name=""
              id=""
              :placeholder="
                $store.state.lan == 'en' ? 'Search for user' : '  '
              "
              v-on:input="SearchUsers()"
              v-model="name"
            />
          </div>
        </div>
        <div class="col-12">
          <div class="table-responsive">
            <table class="table table-light table-striped table-hover">
              <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">{{ translate[$store.state.lang].fname }}</th>
                  <th scope="col">{{ translate[$store.state.lang].lname }}</th>
                  <th scope="col">{{ translate[$store.state.lang].status }}</th>
                  <th scope="col">{{ translate[$store.state.lang].phone }}</th>
                  <th scope="col">
                    {{ translate[$store.state.lang].whatsapp }}
                  </th>
                  <th scope="col">{{ translate[$store.state.lang].freez }}</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in shown_data" :key="index">
                  <th scope="row">{{ item.id }}</th>
                  <td>{{ item.fname }}</td>
                  <td>{{ item.lname }}</td>
                  <td>{{ item.status }}</td>
                  <td dir="ltr">{{ item.country_code_phone + item.phone }}</td>
                  <td dir="ltr">
                    {{ item.country_code_whatsapp + item.whatsapp }}
                  </td>
                  <td>
                    <button
                      type="button"
                      :class="item.status == 'active' ? 'freez' : 'active'"
                    >
                      {{
                        item.status == "active"
                          ? $store.state.lang == "en"
                            ? "Freez"
                            : ""
                          : $store.state.lang == "en"
                          ? "Activate"
                          : ""
                      }}
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </SideBar>
  </div>
</template>

<script>
import SideBar from "@/components/AdminDashboard/SideBar.vue";
export default {
  name: "admin-users",
  components: {
    SideBar,
  },
  data() {
    return {
      name: "",
      translate: {
        en: {
          fname: "First name",
          lname: "Last name",
          status: "Status",
          phone: "Phone",
          whatsapp: "Whatsapp",
          freez: "Freez",
        },
        ar: {
          fname: " ",
          lname: " ",
          status: "",
          phone: " ",
          whatsapp: "",
          freez: "",
        },
      },
      shown_data: {},
      users: {
        0: {
          id: 1,
          fname: "",
          lname: "",
          status: "active",
          country_code_phone: "+963",
          phone: "933097404",
          country_code_whatsapp: "+963",
          whatsapp: "933097404",
        },
        1: {
          id: 2,
          fname: " ",
          lname: "",
          status: "active",
          country_code_phone: "+963",
          phone: "933097404",
          country_code_whatsapp: "+963",
          whatsapp: "933097404",
        },
        2: {
          id: 3,
          fname: "",
          lname: "",
          status: "unactive",
          country_code_phone: "+963",
          phone: "933097404",
          country_code_whatsapp: "+963",
          whatsapp: "933097404",
        },
      },
    };
  },
  methods: {
    SearchUsers() {
      this.shown_data = {};
      if (this.name == "") {
        this.shown_data = this.users;
      } else {
        if (this.name.search(/[\\]/) != -1) {
          console.log(this.name.search(/[\\]/));
          return;
        } else {
          var j = 0;
          for (var i = 0; i < Object.keys(this.users).length; i++) {
            var fullname = this.users[i].fname + " " + this.users[i].lname;
            if (fullname.search(this.name) == 0) {
              this.shown_data[j] = this.users[i];
              j++;
            }
          }
        }
      }
    },
  },
  mounted() {
    this.SearchUsers();
  },
};
</script>

