<template>
  <div class="add-places">
    <SideBar>
      <div class="card w-100 p-2 my-2">
        <div class="row w-100">
          <div class="col-12 my-2">
            <h1>{{ translate[$store.state.lang].add }}</h1>
          </div>
          <div class="col-6 my-2">
            <input
              type="text"
              class="myinp"
              style="cursor: none; pointer-events: none"
              :placeholder="translate[$store.state.lang].name"
              v-model="name"
            />
          </div>
          <div class="col-6 my-2">
            <input
              type="text"
              class="myinp"
              :placeholder="translate[$store.state.lang].sell"
              v-model="price_sell"
            />
          </div>
          <div class="col-6 my-2">
            <input
              type="text"
              class="myinp"
              :placeholder="translate[$store.state.lang].rent"
              v-model="price_rent"
            />
          </div>
          <div class="col-12 my-2">
            <h3>{{ translate[$store.state.lang].map }}</h3>
          </div>
          <div class="col-12 flex-center my-2">
            <input type="text" class="w-100 myinp" id="autocompleteInput" />
          </div>
          <div class="col-12 my-2" style="padding-bottom: 500px">
            <div
              class="card w-100"
              style="background-color: unset; border: none"
            >
              <div class="map" id="map"></div>
            </div>
          </div>
          <div class="col-12 my-4">
            <button class="mybtn" @click="AddPlace()">
              {{ translate[$store.state.lang].save }}
            </button>
          </div>
          <div class="col-12 my-2">
            <h3>{{ translate[$store.state.lang].places }}</h3>
          </div>
          <div class="col-12">
            <div class="table-responsive">
              <table class="table table-striped table-light table-hover">
                <thead>
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">{{ translate[$store.state.lang].name }}</th>
                    <th scope="col">{{ translate[$store.state.lang].sell }}</th>
                    <th scope="col">{{ translate[$store.state.lang].rent }}</th>
                    <th scope="col">
                      {{ translate[$store.state.lang].delete }}
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in places" :key="index">
                    <th scope="row">{{ item.id }}</th>
                    <td>{{ item.name }}</td>
                    <td>{{ item.price_sell }}</td>
                    <td>{{ item.price_rent }}</td>
                    <td>
                      <button type="button" class="mybtn">
                        {{ translate[$store.state.lang].delete }}
                      </button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </SideBar>
  </div>
</template>

<script>
import SideBar from "@/components/AdminDashboard/SideBar.vue";
import { Loader } from "@googlemaps/js-api-loader";
import customMarkerImage from "@/assets/location.png";
import store from "@/store";
import axios from "axios";
export default {
  name: "add-places",
  components: {
    SideBar,
  },
  data() {
    return {
      lating: {},
      price_sell: "",
      price_rent: "",
      name: "",
      city_check: 0,
      translate: {
        en: {
          add: "Add place",
          map: "Add place on map",
          sell: "Sell price",
          rent: "Rent price",
          name: "Place name",
          save: "Save",
          places: "Places",
          delete: "Delete",
        },
        ar: {
          add: " ",
          map: "   ",
          sell: " ",
          rent: " ",
          name: " ",
          save: "",
          places: "",
          delete: "",
        },
      },
      places: [],
      places_page: 1,
      places_max_page: 0,
    };
  },
  methods: {
    async initMap() {
      this.loader = new Loader({
        apiKey: "AIzaSyCRm9FZWayulRn3b4fv3R3GZE3ZPGAti78",
        version: "weekly", // Specify the desired version of the Google Maps API
        libraries: ["places"],
      });
      this.loader.load().then(() => {
        // auto complete
        const autocomplete = new window.google.maps.places.Autocomplete(
          document.getElementById("autocompleteInput")
        );
        this.autocomplete = autocomplete;
        autocomplete.addListener("place_changed", () => {
          const place = autocomplete.getPlace();
          if (place.geometry && place.geometry.location) {
            this.error = "";
            const lat = place.geometry.location.lat();
            const lng = place.geometry.location.lng();

            // Do something with the latitude and longitude values
            this.map.setCenter({ lat, lng });
            this.map.setZoom(15);
          } else {
            this.error =
              store.state.lang == "en"
                ? "You should choose the place from the autocomplete menu"
                : store.state.lang == "ar"
                ? "     "
                : "";
          }
        });

        // The Google Maps API has been loaded
        // You can now use the map and markers
        this.map = new window.google.maps.Map(document.getElementById("map"), {
          center: { lat: 33.2232, lng: 43.6793 },
          zoom: 7,
        });

        let markers = []; // Array to store the markers

        // Function to add new markers based on zoom level
        const addMarker = (latLng) => {
          if (markers.length == 1) {
            // Marker already exists, remove it
            markers[0].setMap(null);
            markers = [];
          }
          // Marker does not exist, create it
          const marker = new window.google.maps.Marker({
            position: latLng,
            map: this.map,
            icon: {
              url: customMarkerImage, // Set the custom marker image URL
              scaledSize: new window.google.maps.Size(35, 35), // Set the desired size of the marker image
            },
          });
          this.lating = { lat: latLng.lat(), lng: latLng.lng() };
          markers.push(marker);
          this.getLocationDetails();
        };

        // Add event listener for zoom change
        this.map.addListener("click", (event) => {
          addMarker(event.latLng);
        });
      });
    },
    async getLocationDetails() {
      const url = `https://nominatim.openstreetmap.org/reverse?format=json&lat=${this.lating.lat}&lon=${this.lating.lng}`;

      try {
        const response = await axios.get(url);
        const result = response.data;

        if (result && result.address) {
          this.extractLocationDetails(result.address);
        } else {
          this.location = null;
          alert("No location found for the provided coordinates.");
        }
      } catch (error) {
        console.error("Error fetching location details:", error);
        alert("Error fetching location details. Please try again.");
      }
    },
    extractLocationDetails(address) {
      this.city_check = 0;
      var text = address.state;
      var index = text.search("Governorate");
      text = text.substring(0, index - 1);
      if (
        address.city == store.state.citys["en"][store.state.city_id - 1].name ||
        text == store.state.citys["en"][store.state.city_id - 1].name
      ) {
        const governorate = address.state || address.city || "";
        const street =
          address.road ||
          address.neighbourhood ||
          address.quarter ||
          address.village ||
          "";

        this.name = governorate + ", " + street;
        this.city_check = 1;
      } else {
        alert(
          store.state.lang == "en"
            ? "You should choose different location"
            : "    "
        );
      }
    },
    AddPlace() {
      if (this.name == "") {
        alert(
          store.state.lang == "en"
            ? "You should choose street or area"
            : "   "
        );
        return;
      } else if (this.price_rent == "") {
        alert(
          store.state.lang == "en"
            ? "You should enter rent price"
            : "    "
        );
        return;
      } else if (this.price_sell == "") {
        alert(
          store.state.lang == "en"
            ? "You should enter sell price"
            : "    "
        );
        return;
      }
      const token = store.state.token;
      axios(store.state.server_name + "admin/add-place", {
        method: "post",
        headers: { Authorization: `Bearer ${token}` },
        data: {
          city_id: store.state.city_id,
          name: this.name,
          price_rent: this.price_rent,
          price_sell: this.price_sell,
        },
      })
        .then((response) => {
          alert(response.data.msg);
          this.price_rent = "";
          this.price_sell = "";
          this.name = "";
          this.places_page = 1;
          this.places_max_page = 0;
          this.getAllPlcaes();
          this.initMap();
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getAllPlcaes() {
      axios
        .get(
          store.state.server_name +
            "api/get-all-places?page=" +
            this.places_page
        )
        .then((response) => {
          var data = response.data.place.data;
          this.places_max_page = response.data.place.last_page;
          this.places_page++;
          for (var i = 0; i < data.length; i++) {
            if (store.state.city_id == data[i].city_id)
              this.places.push(data[i]);
          }
          if (this.places_page <= this.places_max_page) {
            setTimeout(() => {
              this.getAllPlcaes();
            }, 100);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  mounted() {
    this.initMap();
    this.getAllPlcaes();
  },
};
</script>

