<template>
  <div class="company-requests">
    <SideBar>
      <div
        class="row w-100 bg-light p-5 flex-center"
        ref="target2"
        style="border-radius: 0px 0px 10px 10px; border: none"
      >
        <div
          class="col-12 add-real mb-5 w-100"
          style="height: fit-content; width: 100%"
        >
          <div
            class="w-100 h-100"
            :class="$store.state.lang == 'en' ? 'text-start' : 'text-end'"
          >
            {{
              $store.state.lang == "en"
                ? "Requests"
                : $store.state.lang == "ar"
                ? ""
                : ""
            }}
            ({{ realnum }})
          </div>
        </div>
        <div class="col-12" v-if="Object.keys(realestates).length == 0">
          {{
            $store.state.lang == "en"
              ? "No companies has been added yet"
              : $store.state.lang == "ar"
              ? "    "
              : ""
          }}
        </div>
        <div
          class="col-4"
          v-for="offer in realestates[current_page]"
          v-bind:key="offer.id"
          dir="ltr"
        >
          <CompaniesCard
            class="my-5"
            :id="offer.id"
            :img1="offer.img1"
            :name="offer.name"
            :phone="offer.phone"
            :email="offer.email"
            :fav="offer.fav"
            type="company"
            dashboard="1"
            :statics="offer.statics"
            :gender="offer.gender"
            isRequest="true"
          ></CompaniesCard>
        </div>
        <div class="col-12">
          <div class="row" dir="ltr">
            <div class="col-12">
              <div
                class="container pagi d-flex justify-contnet-center align-items-center mb-5"
              >
                <div class="mx-3">
                  <button
                    type="button"
                    class="left-arrow"
                    @click="
                      if (current_page == 0) {
                        current_page = realestates.max_pages - 1;
                      } else {
                        current_page--;
                      }
                      scrollToElement();
                    "
                  >
                    <Icon icon="material-symbols:arrow-forward-ios-rounded" />
                  </button>
                </div>
                <span
                  class="mx-3 d-flex justify-content-center align-items-center"
                  v-for="(element, index) in realestates"
                  v-bind:key="index"
                >
                  <button
                    v-if="element != realestates.max_pages"
                    type="button"
                    class="page_number"
                    :style="{
                      backgroundColor:
                        current_page == index ? '#e57c23' : 'white',
                    }"
                    @click="
                      current_page = index;
                      scrollToElement();
                    "
                  >
                    {{ parseInt(index) + 1 }}
                  </button>
                </span>
                <div class="mx-3">
                  <button
                    type="button"
                    class="right-arrow"
                    @click="
                      if (current_page == realestates.max_pages - 1) {
                        current_page = 0;
                      } else {
                        current_page++;
                      }
                      scrollToElement();
                    "
                  >
                    <Icon icon="material-symbols:arrow-forward-ios-rounded" />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </SideBar>
  </div>
</template>

<script>
import SideBar from "@/components/AdminDashboard/SideBar.vue";
import router from "@/router";
import CompaniesCard from "@/components/CompaniesCard.vue";
import { Icon } from "@iconify/vue";
import axios from "axios";
import store from "@/store";

export default {
  name: "company-requests",
  components: {
    SideBar,
    CompaniesCard,
    Icon,
  },
  data() {
    return {
      realnum: 0,
      realestates: {
        0: {},
        max_pages: 1,
      },
      current_page: 0,
      // api
      page: 1,
      max_pages: 0,
      counter: 0,
    };
  },
  methods: {
    CheckRequest() {
      router.push("/company-requests");
    },
    scrollToElement() {
      this.$refs.target2.scrollIntoView({
        behavior: "smooth",
      });
    },
    getData() {
      axios
        .get(
          store.state.server_name + "admin/get-all-pending-companies?page=",
          {
            headers: { Authorization: `Bearer ${store.state.token}` },
          }
        )
        .then((response) => {
          this.max_pages = response.data.companies.last_page;
          const data = response.data.companies.data;
          for (let i = 0; i < data.length; i++) {
            this.realestates[this.counter][i] = {
              id: data[i].id,
              img1: data[i].profile_image,
              name: data[i].company_name,
              phone: data[i].phone,
              fav: 0,
              email: data[i].email,
              gender: data[i].gender,
            };
            this.realnum++;
          }
          this.counter++;
          this.realestates.max_pages = this.max_pages;
          this.page++;
          if (this.page <= this.max_pages) {
            setTimeout(() => {
              this.getData();
            }, 100);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  mounted() {
    this.getData();
  },
};
</script>

