<template>
  <div class="project-check">
    <SideBar>
      <div
        class="row w-100 bg-light p-5 flex-center"
        ref="target2"
        style="border-radius: 0px 0px 10px 10px; border: none"
      >
        <div
          class="col-12 add-real mb-5 w-100"
          style="height: fit-content; width: 100%"
        >
          <div
            class="w-100 h-100"
            :class="$store.state.lang == 'en' ? 'text-start' : 'text-end'"
          >
            {{
              $store.state.lang == "en"
                ? "Requests"
                : $store.state.lang == "ar"
                ? ""
                : ""
            }}
            ({{ realnum }})
          </div>
        </div>
        <div class="col-12" v-if="Object.keys(realestates).length == 0">
          {{
            $store.state.lang == "en"
              ? "No Projects has been added, Please add Project"
              : $store.state.lang == "ar"
              ? "       "
              : ""
          }}
        </div>
        <div
          class="col-4 flex-center"
          dir="ltr"
          v-else
          v-for="real in realestates[current_page]"
          :key="real.id"
        >
          <RealestateCard
            :id="real.id"
            :name="real.name"
            :price="real.price"
            :location="real.location"
            :space="real.space"
            :type="real.type"
            :bedrooms="real.bedrooms"
            :bathrooms="real.bathrooms"
            :src="real.src"
            :state="real.state"
            :fav="real.fav"
            :phone="real.phone"
            :email="real.email"
            page_type="real-estate"
            page_type2="dashboard"
            :statics="real.statics"
          ></RealestateCard>
        </div>
        <div class="col-12">
          <div class="row" dir="ltr">
            <div class="col-12">
              <div
                class="container pagi d-flex justify-contnet-center align-items-center mb-5"
              >
                <div class="mx-3">
                  <button
                    type="button"
                    class="left-arrow"
                    @click="
                      if (current_page == 0) {
                        current_page = realestates.max_pages;
                      } else {
                        current_page--;
                      }
                      scrollToElement();
                    "
                  >
                    <Icon icon="material-symbols:arrow-forward-ios-rounded" />
                  </button>
                </div>
                <span
                  class="mx-3 d-flex justify-content-center align-items-center"
                  v-for="(element, index) in realestates"
                  v-bind:key="index"
                >
                  <button
                    v-if="element != realestates.max_pages"
                    type="button"
                    class="page_number"
                    :style="{
                      backgroundColor:
                        current_page == index ? '#e57c23' : 'white',
                    }"
                    @click="
                      current_page = index;
                      scrollToElement();
                    "
                  >
                    {{ index }}
                  </button>
                </span>
                <div class="mx-3">
                  <button
                    type="button"
                    class="right-arrow"
                    @click="
                      if (current_page == realestates.max_pages) {
                        current_page = 1;
                      } else {
                        current_page++;
                      }
                      scrollToElement();
                    "
                  >
                    <Icon icon="material-symbols:arrow-forward-ios-rounded" />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </SideBar>
  </div>
</template>

<script>
import SideBar from "@/components/AdminDashboard/SideBar.vue";
import RealestateCard from "@/components/RealestateCard.vue";
import { Icon } from "@iconify/vue";
import axios from "axios";
import store from "@/store";

export default {
  name: "project-check",
  components: {
    SideBar,
    RealestateCard,
    Icon,
  },
  data() {
    return {
      realnum: 0,
      realestates: {
        0: {},
        max_pages: 1,
      },
      current_page: 0,
      page: 1,
      loop_page: 0,
      max_pages: 0,
    };
  },
  methods: {
    scrollToElement() {
      this.$refs.target2.scrollIntoView({
        behavior: "smooth",
      });
    },
    realcount() {
      for (let index = 0; index < this.realestates.max_pages; index++) {
        for (let j = 0; j < Object.keys(this.realestates[index]).length; j++) {
          this.realnum++;
        }
      }
    },
    getData() {
      axios
        .get(
          store.state.server_name +
            "admin/get-all-pending-real-estates?page=" +
            this.page,
          {
            headers: { Authorization: `Bearer ${store.state.token}` },
          }
        )
        .then((response) => {
          console.log(response);
          this.max_pages = response.data.real_estates.last_page;
          this.page++;
          const data = response.data.real_estates.data;
          for (let i = 0; i < data.length; i++) {
            this.realestates[this.loop_page][i] = {
              id: data[i].id,
              name: data[i].name,
              price: data[i].price,
              phone: data[i].phone,
              email: data[i].email,
              location: data[i].location,
              space: data[i].space_of_real,
              type: data[i].rent_kind == null ? "sell" : "rent",
              bedrooms: data[i].num_of_bedrooms,
              bathrooms: data[i].num_of_bath,
              state: "review",
              fav: data[i].is_fav,
              src: data[i].image,
            };
            this.realnum++;
          }
          this.loop_page++;
          if (this.page <= this.max_pages) {
            setTimeout(() => {
              this.getData();
            }, 100);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  mounted() {
    this.realcount();
    this.getData();
  },
};
</script>

