      flag: "iq",
      flag_whatsapp: "iq",
      description: "",
      //
      city_index: "",
      places_page: 1,
      places_max_page: 0,
      city_check: 0,
      autocomplete: null,
    };
  },
  methods: {
    async initMap() {
      this.loader = new Loader({
        apiKey: "AIzaSyCRm9FZWayulRn3b4fv3R3GZE3ZPGAti78",
        version: "weekly", // Specify the desired version of the Google Maps API
        libraries: ["places"],
      });
      this.loader.load().then(() => {
        // auto complete
        const autocomplete = new window.google.maps.places.Autocomplete(
          document.getElementById("autocompleteInput")
        );
        this.autocomplete = autocomplete;
        autocomplete.addListener("place_changed", () => {
          const place = autocomplete.getPlace();
          if (place.geometry && place.geometry.location) {
            this.error = "";
            const lat = place.geometry.location.lat();
            const lng = place.geometry.location.lng();

            // Do something with the latitude and longitude values
            this.map.setCenter({ lat, lng });
            this.map.setZoom(15);
          } else {
            this.error =
              store.state.lang == "en"
                ? "You should choose the place from the autocomplete menu"
                : store.state.lang == "ar"
                ? "     "
                : "";
          }
        });

        // The Google Maps API has been loaded
        // You can now use the map and markers
        this.map = new window.google.maps.Map(document.getElementById("map"), {
          center: { lat: 33.2232, lng: 43.6793 },
          zoom: 7,
        });

        let markers = []; // Array to store the markers

        // Function to add new markers based on zoom level
        const addMarker = (latLng) => {
          if (markers.length == 1) {
            // Marker already exists, remove it
            markers[0].setMap(null);
            markers = [];
          }
          // Marker does not exist, create it
          const marker = new window.google.maps.Marker({
            position: latLng,
            map: this.map,
            icon: {
              url: customMarkerImage, // Set the custom marker image URL
              scaledSize: new window.google.maps.Size(35, 35), // Set the desired size of the marker image
            },
          });
          this.lating = { lat: latLng.lat(), lng: latLng.lng() };
          markers.push(marker);
          this.getLocationDetails();
        };

        // Add event listener for zoom change
        this.map.addListener("click", (event) => {
          addMarker(event.latLng);
        });
      });
    },
    async getLocationDetails() {
      const url = `https://nominatim.openstreetmap.org/reverse?format=json&lat=${this.lating.lat}&lon=${this.lating.lng}`;

      try {
        const response = await axios.get(url);
        const result = response.data;

        if (result && result.address) {
          this.extractLocationDetails(result.address);
        } else {
          this.location = null;
          alert("No location found for the provided coordinates.");
        }
      } catch (error) {
        console.error("Error fetching location details:", error);
        alert("Error fetching location details. Please try again.");
      }
    },
    extractLocationDetails(address) {
      this.city_check = 0;
      if (address.city == store.state.citys["en"][this.city_index].name) {
        const governorate = address.state || "";
        const street =
          address.road ||
          address.neighbourhood ||
          address.quarter ||
          address.village ||
          "";

        const place = governorate + ", " + street;
        if (place == this.selected_place) {
          this.city_check = 1;
        } else {
          alert(
            store.state.lang == "en"
              ? "You should choose different location"
              : "    "
          );
        }
      } else {
        alert(
          store.state.lang == "en"
            ? "You should choose different location"
            : "    "
        );
      }
    },
    addWorkDay() {
      if (
        this.selected_day == "" ||
        this.selected_start_hour == "" ||
        this.selected_end_hour == ""
      ) {
        alert(
          store.state.lang == "en"
            ? "Some fields are empty"
            : "  "
        );
        return;
      }
      if (this.workDays.length > 0) {
        for (let i = 0; i < this.workDays.length; i++) {
          if (this.workDays[i].day == this.selected_day) {
            alert(
              store.state.lang == "en"
                ? "This day is already taken"
                : "   "
            );
            return;
          }
        }
      }
      this.workDays.push({
        day: this.selected_day,
        start: this.selected_start_hour,
        end: this.selected_end_hour,
      });
    },
    RemoveWorkDay(index) {
      this.workDays.splice(index, 1);
    },
    addRemovePayment(index) {
      if (this.payment_ways.length > 0) {
        for (let i = 0; i < this.payment_ways.length; i++) {
          if (this.payment_ways[i].name == this.Payment[index].name) {
            this.payment_ways.splice(i, 1);
            return;
          }
        }
      }
      this.payment_ways.push(this.Payment[index]);
    },
    getAllPlcaes() {
      axios
        .get(
          store.state.server_name +
            "api/get-all-places?page=" +
            this.places_page
        )
        .then((response) => {
          var data = response.data.place.data;
          this.places_max_page = response.data.place.last_page;
          this.places_page++;
          for (var i = 0; i < data.length; i++) {
            this.places.push(data[i]);
          }
          if (this.places_page <= this.places_max_page) {
            setTimeout(() => {
              this.getAllPlcaes();
            }, 100);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    changePlaces() {
      this.shown_places = [];
      if (this.places.length > 0)
        for (let i = 0; i < this.places.length; i++) {
          if (this.city_id_selected == this.places[i].city_id)
            this.shown_places.push(this.places[i]);
        }
    },
    addCompanyService() {
      const pf_image = document.querySelector("#pf_image");
      const images = document.querySelector("#images");
      const pf_image_file = pf_image.files[0];
      const images_array = images.files;
      if (this.name == "") {
        alert(
          store.state.lang == "en"
            ? "You should enter company name"
            : "   "
        );
        return;
      } else if (this.languages == "") {
        alert(
          store.state.lang == "en"
            ? "You should enter languages"
            : "  "
        );
        return;
      } else if (this.email == "") {
        alert(
          store.state.lang == "en"
            ? "You should enter email"
            : "  "
        );
        return;
      } else if (this.email.search(/^[\w-.]+@([\w-]+.)+[\w-]{2,4}$/) != 0) {
        alert(
          store.state.lang == "en" ? "That's not an email" : "  "
        );
        return false;
      } else if (this.city_id_selected == "") {
        alert(
          store.state.lang == "en"
            ? "You should choose city"
            : "  "
        );
        return;
      } else if (this.place_id_selected == "") {
        alert(
          store.state.lang == "en"
            ? "You should choose place"
            : "  "
        );
        return;
      } else if (this.service_id_selected == "") {
        alert(
          store.state.lang == "en"
            ? "You should choose service"
            : "   "
        );
        return;
      } else if (this.phone == "") {
        alert(
          store.state.lang == "en"
            ? "You should fill the phone"
            : "    "
        );
        return false;
      } else if (this.phone.search(/^[0-9]+$/) != 0) {
        alert(
          store.state.lang == "en"
            ? "That's not a phone number"
            : "   "
        );
        return false;
      } else if (this.whatsApp == "") {
        alert(
          store.state.lang == "en"
            ? "You should fill the whatsapp number"
            : "    "
        );
        return false;
      } else if (this.whatsApp.search(/^[0-9]+$/) != 0) {
        alert(
          store.state.lang == "en"
            ? "That's not a whatsapp number"
            : "   "
        );
        return false;
      } else if (this.workDays.length == 0) {
        alert(
          store.state.lang == "en"
            ? "You should add work days"
            : "   "
        );
        return;
      } else if (this.payment_ways.length == 0) {
        alert(
          store.state.lang == "en"
            ? "You should choose payment way"
            : "   "
        );
        return;
      } else if (Object.keys(this.lating).length == 0) {
        alert(
          store.state.lang == "en"
            ? "You should enter location on map"
            : "    "
        );
        return;
      } else if (pf_image_file == undefined) {
        alert(
          store.state.lang == "en"
            ? "You should add profile image"
            : "    "
        );
        return;
      } else if (images_array.length == 0) {
        alert(
          store.state.lang == "en" ? "You should images" : "  "
        );
        return;
      } else if (this.city_check == 0) {
        alert(
          store.state.lang == "en"
            ? "You didn't choose right place on map"
            : "      "
        );
        return;
      }
      const fd = new FormData();
      fd.append("service_id", this.service_id_selected);
      fd.append("subcripation_id", 1);
      fd.append("city_id", this.city_id_selected);
      fd.append("name", this.name);
      fd.append("place_id", this.place_id_selected);
      fd.append("languages", this.languages);
      fd.append("work_days", JSON.stringify(this.workDays));
      fd.append("payment_ways", JSON.stringify(this.payment_ways));
      fd.append("email", this.email);
      fd.append("phone", this.phone);
      fd.append("country_code_phone", this.code);
      fd.append("whatsApp", this.whatsApp);
      fd.append("country_code_whatsApp", this.code_whatsapp);
      fd.append("country_code_whatsApp", this.code_whatsapp);
      fd.append("profile_image", pf_image_file);
      fd.append("facebook", this.facebook);
      fd.append("twitter", this.twitter);
      fd.append("instagram", this.instagram);
      fd.append("snapchat", this.snapchat);
      fd.append("description", this.description);
      fd.append("video", this.video);
      fd.append("lng", this.lating.lng);
      fd.append("lat", this.lating.lat);
      for (let i = 0; i < images_array.length; i++) {
        fd.append(`images[${i}]`, images_array[i]);
      }

      axios
        .post(store.state.server_name + "admin/add-company-service", fd, {
          headers: {
            Authorization: `Bearer ${store.state.token}`,
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          alert(response.data.msg);
          router.push("/admin-serviceCompanies");
        })
        .catch((error) => {
          if (error.response.status == 500) {
            alert(error.response.data.msg);
          }
        });
    },
  },
  mounted() {
    this.initMap();
    this.getAllPlcaes();
  },
};
