<template>
  <div class="add-service">
    <SideBar>
      <div class="card my-3 p-3 w-75">
        <div class="card-title">
          <h1>{{ translate[$store.state.lang].add }}</h1>
        </div>
        <div class="card-body">
          <div class="row w-100">
            <div class="col-12">
              <input
                type="text"
                name=""
                id=""
                class="myinp"
                :placeholder="translate[$store.state.lang].name"
                v-model="name"
              />
            </div>
            <div
              class="col-12 my-4 d-flex justify-content-start align-items-center"
            >
              {{ translate[$store.state.lang].img }}
              <input
                type="file"
                name=""
                id="image"
                class="mx-3"
                accept="image/*"
              />
            </div>
            <div class="col-12 my-4">
              <button type="button" class="mybtn" @click="AddService()">
                {{ translate[$store.state.lang].save }}
              </button>
            </div>
          </div>
        </div>
      </div>
    </SideBar>
  </div>
</template>

<script>
import SideBar from "@/components/AdminDashboard/SideBar.vue";
import router from "@/router";
import store from "@/store";
import axios from "axios";
export default {
  name: "add-service",
  components: {
    SideBar,
  },
  data() {
    return {
      translate: {
        en: {
          add: "Add service",
          name: "Service name",
          img: "Add image",
          save: "Save",
        },
        ar: {
          add: " ",
          name: " ",
          img: " ",
          save: "",
        },
      },
      name: "",
    };
  },
  methods: {
    AddService() {
      const imgFile = document.querySelector("#image");

      if (this.name == "") {
        alert(
          store.state.lang == "en"
            ? "You should enter service name"
            : "    "
        );
        return;
      } else if (imgFile.files[0] == undefined) {
        alert(
          store.state.lang == "en"
            ? "You should add service image"
            : "    "
        );
        return;
      }
      const formData = new FormData();
      formData.append("name", this.name);
      formData.append("image", imgFile.files[0]);

      axios
        .post(store.state.server_name + "admin/add-service", formData, {
          headers: { Authorization: `Bearer ${store.state.token}` },
        })
        .then((response) => {
          alert(response.data.msg);
          router.push("/admin-services");
        })
        .catch((error) => {
          if (error.response.status == 422) {
            alert(
              store.state.lang == "en"
                ? "Name is not correct"
                : "  "
            );
          } else console.log(error);
        });
    },
  },
};
</script>

<style scoped>
.myinp {
  width: 100%;
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  height: 50px;
  padding: 0px 10px;
}

.myinp:focus {
  outline: none !important;
}

.mybtn {
  background-color: #e57c23;
  color: white;
  border-radius: 10px;
  border: none;
  padding: 10px;
  width: 100%;
}
</style>
