<template>
  <div class="admin-service">
    <SideBar>
      <div
        class="row w-100 bg-light p-5 flex-center"
        ref="target2"
        style="border-radius: 0px 0px 10px 10px; border: none"
      >
        <div
          class="col-12 add-real mb-5 w-100"
          style="height: fit-content; width: 100%"
        >
          <div
            class="w-50 h-100"
            :class="$store.state.lang == 'en' ? 'text-start' : 'text-end'"
          >
            {{
              $store.state.lang == "en"
                ? "Services"
                : $store.state.lang == "ar"
                ? ""
                : ""
            }}
            ({{ Object.keys($store.state.services[$store.state.lang]).length }})
          </div>
          <div
            class="w-50 h-100"
            :class="$store.state.lang == 'en' ? 'text-end' : 'text-start'"
          >
            <button type="button" class="mybtn" @click="CheckRequest()">
              {{ translate[$store.state.lang].add }}
            </button>
          </div>
        </div>
        <div class="col-12 mb-5 w-100" style="height: fit-content; width: 100%">
          <div class="table-responsive">
            <table class="table table-striped table-light table-hover">
              <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">{{ translate[$store.state.lang].img }}</th>
                  <th scope="col">{{ translate[$store.state.lang].name }}</th>
                  <th scope="col">{{ translate[$store.state.lang].edit }}</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(item, index) in $store.state.services[
                    $store.state.lang
                  ]"
                  :key="index"
                >
                  <th scope="row">{{ item.id }}</th>
                  <td><img :src="item.img" alt="" class="service-img" /></td>
                  <td>{{ item.name }}</td>
                  <td>
                    <button type="button" class="active">
                      {{ translate[$store.state.lang].edit }}
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </SideBar>
  </div>
</template>

<script>
import SideBar from "@/components/AdminDashboard/SideBar.vue";
import router from "@/router";
import store from "@/store";
import axios from "axios";
export default {
  name: "admin-service",
  components: {
    SideBar,
  },
  data() {
    return {
      translate: {
        en: {
          img: "Image",
          name: "Name",
          edit: "Edit",
          add: "Add",
        },
        ar: {
          img: "",
          name: "",
          edit: "",
          add: "",
        },
      },
      pageNumber: 1,
      maxPages: 0,
    };
  },
  methods: {
    CheckRequest() {
      router.push("/add-service");
    },
    getData() {
      axios
        .get(
          store.state.server_name +
            "api/get-all-services?page=" +
            this.pageNumber
        )
        .then((response) => {
          this.maxPages = response.data.services.last_page;
          this.pageNumber++;
          if (this.pageNumber <= this.maxPages) {
            setTimeout(() => {
              this.getData();
            }, 100);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  mounted() {
    this.getData();
  },
};
</script>

