<template>
  <div class="admin-cities">
    <SideBar>
      <div
        class="row w-100 bg-light p-5 flex-center"
        ref="target2"
        style="border-radius: 0px 0px 10px 10px; border: none"
      >
        <div
          class="col-12 add-real mb-5 w-100"
          style="height: fit-content; width: 100%"
        >
          <div
            class="w-50 h-100"
            :class="$store.state.lang == 'en' ? 'text-start' : 'text-end'"
          >
            {{
              $store.state.lang == "en"
                ? "Cities"
                : $store.state.lang == "ar"
                ? ""
                : ""
            }}
            ({{ Object.keys($store.state.citys[$store.state.lang]).length }})
          </div>
          <div
            class="w-50 h-100"
            :class="$store.state.lang == 'en' ? 'text-end' : 'text-start'"
          >
            <button type="button" class="mybtn" @click="CheckRequest()">
              {{ translate[$store.state.lang].add }}
            </button>
          </div>
        </div>
        <div class="col-12 mb-5 w-100" style="height: fit-content; width: 100%">
          <div class="table-responsive">
            <table class="table table-striped table-light table-hover">
              <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">{{ translate[$store.state.lang].img }}</th>
                  <th scope="col">{{ translate[$store.state.lang].name }}</th>
                  <th scope="col">{{ translate[$store.state.lang].edit }}</th>
                  <th scope="col">
                    {{ translate[$store.state.lang].special }}
                  </th>
                  <th scope="col">
                    {{ translate[$store.state.lang].add_places }}
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(item, index) in $store.state.citys[$store.state.lang]"
                  :key="index"
                >
                  <th scope="row">{{ item.id }}</th>
                  <td><img :src="item.img" alt="" class="service-img" /></td>
                  <td>{{ item.name }}</td>
                  <td>
                    <button type="button" class="active">
                      {{ translate[$store.state.lang].edit }}
                    </button>
                  </td>
                  <td>
                    <button
                      type="button"
                      class="active"
                      v-if="item.special"
                      @click="make_special(item.id, index)"
                    >
                      {{ translate[$store.state.lang].special }}
                    </button>
                    <button
                      type="button"
                      class="active"
                      v-else
                      @click="make_special(item.id, index)"
                    >
                      {{ translate[$store.state.lang].add_special }}
                    </button>
                  </td>
                  <td>
                    <button
                      type="button"
                      class="active"
                      @click="addPlaces(item.id)"
                    >
                      {{ translate[$store.state.lang].add }}
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </SideBar>
  </div>
</template>

<script>
import router from "@/router";
import SideBar from "@/components/AdminDashboard/SideBar.vue";
import axios from "axios";
import store from "@/store";
export default {
  name: "admin-cities",
  components: {
    SideBar,
  },
  data() {
    return {
      translate: {
        en: {
          img: "Image",
          name: "Name",
          edit: "Edit",
          add: "Add",
          add_places: "Add places",
          special: "Remove from special",
          add_special: "Add to special",
        },
        ar: {
          img: "",
          name: "",
          edit: "",
          add: "",
          add_places: " ",
          special: "  ",
          add_special: "  ",
        },
      },
    };
  },
  methods: {
    CheckRequest() {
      router.push("/add-city");
    },
    addPlaces(id) {
      router.push("/add-places?city_id=" + id);
    },
    make_special(id, index) {
      const token = store.state.token;
      axios(store.state.server_name + "admin/make-city-specail", {
        method: "post",
        data: { id: id },
        headers: { Authorization: `Bearer ${token}` },
      })
        .then((response) => {
          alert(response.data.msg);
          store.state.citys[store.state.lang][index]["special"] == 1
            ? (store.state.citys[store.state.lang][index]["special"] = 0)
            : (store.state.citys[store.state.lang][index]["special"] = 1);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

